class ServerInfo {
  /*
  ** 
  ** state: 
  ** 
  */
  constructor(state){
    this.state = state
  }
  
  //
  SwitchState (state) {
    let StateStr = ''
    switch (state) {
      case 'Pending':
        StateStr = ''
        break;
      case 'Running':
        StateStr = ''
        break;
      case 'Starting':
        StateStr = ''
        break;
      case 'Stopping':
        StateStr = ''
        break;
      case 'Stopped':
        StateStr = ''
        break;
      case 'Writeoff':
        StateStr = ''
        break;
      case 'unknown':
        StateStr = ''
        break;
      case 'liberate':
        StateStr = ''
        break;
      default:
        break;
    }
    return StateStr
  }
}

export default ServerInfo
