import { mapState, mapMutations } from 'vuex'

export default {
  data () {
    return {
      token: '',
      clientHeight: 0,
      isReload: true,
      dialogVisible: true
    }
  },
  computed: {
    ...mapState(['isCollapse', 'isChildCollapse'])
  },
  watch: {
    $route (to, from) {
      //
      this.$nextTick(() => {
        if (this.$refs['myScrollbar']) {
          this.$refs['myScrollbar'].wrap.scrollTop = 0;  //
        }
      })
    }
  },
  destroyed () {
    this.$bus.$off("OnReload")
  },
  created () { },
  mounted () {
    this.$nextTick(() => {
      this.$bus.on("OnReload", () => {
        this.reload()
      })
    })
  },
  methods: {
    ...mapMutations({
      setStoreData: 'setStoreData',
      setBreadcrumb: 'setBreadcrumb'
    }),
    reload () {
      this.isReload = false
      this.$nextTick(() => {
        this.isReload = true
      })
    }
  }
}
